.nav-ul{height: 100%; width: 30%; line-height: 85px;}
.nav-nav{height: 85px; padding: 0;}
.nav-ul li{height: 100%; line-height: 85px;}
nav{background: #000000ab;}
nav a img{height: 9vmin!important; margin-top: -5px;}
.nav-ul li.active{background:#d64545!important}
.nav-link{
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 500;
    margin: 0 20px;
    cursor: pointer;
    color: white;
    padding: 0;
}
.img-logo{padding: 0; margin: 0;}